@import url(https://fonts.googleapis.com/css?family=Montserrat:900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Raleway', sans-serif}.navbar{display:flex;align-items:center;width:100%;position:fixed;top:0;width:100%;z-index:100;justify-content:space-between;list-style-type:none;background-color:#fff4fb;text-align:center}.navbar h2{text-align:center;margin:0;padding:0 10px}.navbar a{color:black;text-decoration:none;text-align:right}.navbar ul{text-align:right;float:right;padding:10px}.navbar li{display:inline;padding:0 20px;color:black}.navbar h2{text-align:left;font-family:'Montserrat', sans-serif}.brand{display:flex}.home-container{position:relative;margin:0 auto;width:55%}.title{background:linear-gradient(to bottom, #eee, rgba(222,112,6,0.2), #de7006),url(https://media1.tenor.com/images/dd8906efa73ed03aa6aa0ceed9e2cbbb/tenor.gif?itemid=17770450);-webkit-filter:contrast(1.5);filter:contrast(1.5);background-attachment:fixed;-webkit-text-fill-color:transparent;-webkit-background-clip:text;text-align:center;text-align:center;margin:0 auto;margin-top:250px;font-family:'Montserrat', sans-serif;font-size:100px}.founder{text-align:center;display:flex;margin-top:200px;justify-content:center}.founderbtn{display:flex;align-items:center;text-align:center;justify-content:center;width:100%;height:auto;border-radius:5px;color:white;cursor:pointer;background-color:#fec7ea;padding-right:15px;transition:all 400ms ease}.founderbtn:hover{background-color:#f59ed5}.founder h2{font-family:'Montserrat', sans-serif;margin:0}.founder img{width:100%;max-width:50%;border-radius:5px;margin-left:15px;height:auto}.models{-webkit-column-count:3;column-count:3;margin:0 auto;grid-column-gap:1.5em;-webkit-column-gap:1.5em;column-gap:1.5em;width:100%;margin-top:13px}.item{background-color:#eee;display:inline-block;margin:10px;width:100%;border-radius:5px}.modelbtn{display:flex;align-items:center;text-align:center;justify-content:center;width:100%;height:auto;padding-right:10px;margin-top:40px;border-radius:5px;cursor:pointer;background-color:#fec7ea;color:white;transition:all 400ms ease}.modelbtn h2{font-family:'Montserrat', sans-serif}.modelbtn:hover{background-color:#f59ed5}.contact{display:flex;justify-content:space-around}.footer button{background-color:#fec7ea;margin:10px;color:white;cursor:pointer;font-size:30px;border:none;width:55px;border-radius:50px;padding:10px;transition:all 400ms ease}.footer button:hover{background-color:#f59ed5}.contact-head{padding-top:50px;text-align:center;margin:0 auto}.footer{position:static;left:0;bottom:0;padding:10px;width:100%;color:white;text-align:center}.featured-title{color:black;-webkit-text-fill-color:white;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:black;text-align:center;text-align:center;margin:0 auto;margin-top:150px;font-family:'Montserrat', sans-serif;font-size:70px}.featured-items{margin:0 auto;margin-top:40px;width:60%;line-height:0;-webkit-column-count:5;-webkit-column-gap:0px;-moz-column-count:5;-moz-column-gap:0px;-webkit-column-count:3;column-count:3;grid-column-gap:15px;-webkit-column-gap:15px;column-gap:15px}.featured-items img{width:100% !important;border-radius:5px;margin:7.5px 0;height:auto !important}.featured-containter{display:flex;justify-content:center}.burger{display:none}.resNav{display:none;position:fixed}@media screen and (max-width: 759px){.burger{display:block;position:static;padding:10px;cursor:pointer}.navbar li{display:none;text-align:center}.resNav{display:block;position:fixed;top:-60px;width:100%;background-color:#fff4fb;padding:0px;z-index:99;list-style-type:none;transition:all 300ms ease}.resNav li{list-style-type:none}.resNav a{text-decoration:none;color:black}.home-container{width:100%}}

