@import url("https://fonts.googleapis.com/css?family=Montserrat:900");

* {
  font-family: 'Raleway', sans-serif;
}

.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  justify-content: space-between;
  list-style-type: none;
  background-color: rgba(255,244,251,1);
  text-align: center;
}

.navbar h2 {
  text-align: center;
  margin: 0;
  padding: 0 10px;
}

.navbar a {
  color: black;
  text-decoration: none;
  text-align: right;
}

.navbar ul {
  text-align: right;
  float: right;
  padding: 10px;
}

.navbar li {
  display: inline;
  padding: 0 20px;
  color: black;
}

.navbar h2 {
  text-align: left;
  font-family: 'Montserrat', sans-serif;
}

.brand {
  display: flex;
}

.home-container {
  position: relative;
  margin: 0 auto;
  width: 55%;
}

